/* styles.css */

body {
    background-color: #2b2b2b; /* Dark background */
    color: #ffffff; /* White text */
    /* size: 2.5in 4in; */
  }
  
  .App {
    max-width: 600px;
    margin: auto;
    padding: 20px;
    text-align: center;
    font-family: "Arial", sans-serif;
  }
  
  h1 {
    color: #fbc02d; /* Dark yellow for headings */
  }
  
  .passwordCopyBox {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .passwordCopyBox input {
    margin-right: 10px;
    padding: 10px;
    width: 300px;
    box-sizing: border-box;
    background-color: #424242; /* Dark grey for input background */
    color: #ffffff;
    border: 1px solid #555;
    transition: border-color 0.3s ease, color 0.3s ease;
    font-size: 1.2em;
  }
  
  .passwordCopyBox button {
    padding: 11px;
    background-color: #fbc02d; /* Green button */
    color: #ffffff;
    border: none;
    cursor: pointer;
    transition: background-color 01s ease, color 1s ease;
    font-size: 1.2em;
  }
  
  .passwordCopyBox button:hover {
    background-color: #fbe70a; /* Dark green on hover */
  }
  
  .passwordCopyBox button:active {
    background-color: #fbe70a; /* Dark green on click */
  }
  
  .passwordLength {
    margin: 40px;
  }
  .passwordLength input {
    width: 50%;
  }
  .passwordLength {
    color: #fbc02d;
  }
  
  .incNumberCharacters {
    margin-top: 20px;
  }
  
  .incNumberCharacters button {
    padding: 15px;
    color: #ffffff;
    border-radius: 5%;
    cursor: pointer;
    transition: background-color 0.5s ease, color 0.5s ease;
    font-size: 1.2em;
    margin: 0 20px;
  }
  